import React from "react";
import { useTranslation } from "react-i18next";
import { TextField, FormLabel, FormHelperText } from "@mui/material";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs";
import { DatePicker } from "@mui/x-date-pickers/DatePicker";

const renderInput = (props) => {
  return <TextField {...props} variant="outlined" />;
};

export const SurveyActiveFromTo = ({
  error,
  surveyActiveFrom,
  surveyActiveTo,
  onSurveyActiveFromChanged,
  onSurveyActiveToChanged,
  disabled,
}) => {
  const { t } = useTranslation("manage");

  return (
    <LocalizationProvider dateAdapter={AdapterDayjs}>
      <FormLabel>
        {t("label.survey_active")}
      </FormLabel>
      <DatePicker
        disabled={disabled}
        label={t("label.from")}
        value={surveyActiveFrom}
        onChange={onSurveyActiveFromChanged}
        renderInput={renderInput}
      />
      <DatePicker
        disabled={disabled}
        label={t("label.to")}
        value={surveyActiveTo}
        onChange={onSurveyActiveToChanged}
        renderInput={renderInput}
      />
      {error && (
        <FormHelperText style={{ color: "red" }}>{error}</FormHelperText>
      )}
    </LocalizationProvider>
  );
};
