import React from "react";
import { useTranslation } from "react-i18next";
import {
  FormControl,
  FormLabel,
  FormControlLabel,
  Checkbox,
} from "@mui/material";
import { LANGUAGE } from "~/constants/language";
import { EducationPanel } from "../EducationPanel";

export const AdditionalLanguages = ({
  baseLanguage,
  onAdditionalLanguagesChanged,
  additionalLanguages,
  disabled,
}) => {
  const { t } = useTranslation("manage");

  return (
    <FormControl>
      <FormLabel id="additional-languages-label">
        {t("label.additional_languages")}
        <EducationPanel htmlFilePath="/education/createSurvey/additionalLanguages/en.html" />
      </FormLabel>
      <FormControlLabel
        control={
          <Checkbox
            disabled={baseLanguage == LANGUAGE.EN || disabled}
            checked={additionalLanguages.indexOf(LANGUAGE.EN) > -1}
            onChange={onAdditionalLanguagesChanged}
            name={LANGUAGE.EN}
          />
        }
        label={t("language.en")}
      />
      <FormControlLabel
        control={
          <Checkbox
            disabled={baseLanguage == LANGUAGE.DE || disabled}
            checked={additionalLanguages.indexOf(LANGUAGE.DE) > -1}
            onChange={onAdditionalLanguagesChanged}
            name={LANGUAGE.DE}
          />
        }
        label={t("language.de")}
      />
      <FormControlLabel
        control={
          <Checkbox
            disabled={baseLanguage == LANGUAGE.AR || disabled}
            checked={additionalLanguages.indexOf(LANGUAGE.AR) > -1}
            onChange={onAdditionalLanguagesChanged}
            name={LANGUAGE.AR}
          />
        }
        label={t("language.ar")}
      />
    </FormControl>
  );
};
